body {
    background: #0077B6;
}

h1 {
    font-size: 2rem
}

.login-container {
    border: 1px solid black;
    border-radius: 100px;
    margin: auto;
    box-sizing: border-box;
    padding: 3rem;
    background: #CAF0F8;
    width: 50%;
    height: 600px;
    font-family: Verdana;
    text-align: center;
    font-size: 1.25rem;
    font-weight: bolder;
    display: flex;
    flex-wrap: wrap;
}

.login-title {
    padding: 5px;
    margin: 5px;
    flex: 2;
}

.login-item {
    width: 90%;
    display: flex;
    padding: 10px;
    margin: 5px;
    background: lightblue;
}

.login-btn {
background: lightblue;
border: 1px;
padding: 15px;
width: 100px;
border-radius: 15px;
font-size: 1.25rem;
}

.login-btn:hover:enabled {
    background: #48cae4;
    cursor: pointer;
}

.login-input{
    font-size: 1.25rem;
    border-radius: 15px;
    margin: 5px;
    padding: 4px;
}

.show{
    display:block
}

.hide{
    display:none
}


.dashboard-container {
    float: left;
    border: 1px solid black;
    border-radius: 100px;
    margin: auto;
    box-sizing: border-box;
    padding: 3rem;
    background: #CAF0F8;
    width: 50%;
    font-family: Verdana;
    text-align: left;
    font-size: 1.25rem;
    font-weight: bolder;
    display: block;
    flex-wrap: nowrap;
}

.dashboard-item {
    width: 90%;
    padding: 5px;
    border: 1px solid;
    border-radius: 15px;
    font-family: Verdana;
    text-align: left;
    font-size: 1.25rem;
    font-weight: bolder;
    }


.dashboard-entry {
    width: 100%;
    padding: 0px;
    border: 0px solid;
}

.dashboard-entry-input {
        width: 50%;
        padding: 5px;
        border: 1px solid;
        border-radius: 15px;
        font-family: Verdana;
        text-align: left;
        font-size: 1.25rem;
        font-weight: bolder;}

.dashboard-clickable {
    border: 0px;
    border-radius: 15px;
    background-color: #48CAE4;
    font-family: Verdana;
    text-align: left;
    font-size: 1.25rem;
    font-weight: bolder;
}

.dashboard-clickable:hover {
    background-color: #0096C7;
    cursor: pointer;
}

.instructions{
    background-color: #164b68;
    color: white;
    font-size: 0.80rem;
    font-family: Verdana;
    border-radius: 15px;
    margin: auto;
}

.offscreen{
    display: none;
}